:root{
  --primary: #F9B;
  --primary-light: #FCD;
  --primary-dark: #D65C85;
  --on-primary: black;

  --secondary: #005050;
  --secondary-light: #3C7D7D;
  --secondary-dark: #002929;
  --on-secondary: white;

  --surface: #eee;
  --on-surface: black;

  --card: white;
  --on-card: black;

  --divider: rgba(0,0,0,0.12);

  --rainbow-red: #E42B2B;
  --rainbow-orange: #FF8829;
  --rainbow-yellow: #FFCA29;
  --rainbow-green: #008043;
  --rainbow-blue: #005DD6;
  --rainbow-purple: #752E84;
  --on-rainbow: white;

  --rainbow-light-red: #E45151;
  --rainbow-light-orange: #FF8450;
  --rainbow-light-yellow: #FFA850;
  --rainbow-light-green: #00805E;
  --rainbow-light-blue: #006DAF;
  --rainbow-light-purple: #755382;
  --on-rainbow-light: black;

  --rainbow-dark-red: #391010;
  --rainbow-dark-orange: #2C1A03;
  --rainbow-dark-yellow: #221F00;
  --rainbow-dark-green: #032C0F;
  --rainbow-dark-blue: #111D3A;
  --rainbow-dark-purple: #310E37;
  --on-rainbow-dark: white;

  --font-weight-light: 200;
  --font-weight-normal: 400;
  --font-weight-medium: 500;
}

body {
  margin: 0;
  padding: 0;
  font-size: 0;
  color: #1d1d1f;
}

body,
input,
textarea,
select,
button {
  font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen-Sans, Oxygen, Ubuntu, Cantarell, 'Helvetica Neue', Helvetica, Arial, sans-serif, 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol';
  font-synthesis: none;
  -moz-font-feature-settings: 'kern';
  font-feature-settings: 'kern';
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  direction: ltr;
  text-align: left;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

b,
strong {
  font-weight:600;
}

h1,
.typography-hero-headline {
  font-size: 96px;
  line-height: 1;
  font-weight: 600;
  letter-spacing: -.00144em;
  color: var(--secondary-dark);
}
h2,
.typography-section-headline {
  font-size: 72px;
  line-height: 1.05556;
  font-weight: 600;
  letter-spacing: -.00086em;
  color: var(--secondary-dark);
}
h3.elevated,
.typography-headline-elevated {
  font-size: 64px;
  line-height: 1.0625;
  font-weight: 600;
  letter-spacing: -.009em;
  color: var(--secondary-dark);
}
h3,
.typography-headline {
  font-size: 48px;
  line-height: 1.08349;
  font-weight: 600;
  letter-spacing: -.003em;
  color: var(--secondary-dark);
}
.subtitle1,
.typography-eyebrow {
  font-size: 24px;
  line-height: 1.16667;
  font-weight: 600;
  letter-spacing: .009em;
  color: var(--secondary-dark);
}
.body1,
.typography-product-lockup {
  font-size: 21px;
  line-height: 1.381;
  font-weight: 400;
  letter-spacing: .011em;
  color: var(--secondary-dark);
}
p,
pre,
input[type="text"],
input[type="number"],
input[type="password"],
textarea,
.body2{
  font-size: 17px;
  line-height: 1.47059;
  font-weight: 400;
  letter-spacing: -.022em;
  font-style: normal;
  color: var(--secondary-dark);
}

hr {
  border: none;
  height: 1px;
  background: var(--divider);
  margin: 32px 0;
}

